
import { Flex, Heading, Text, Image, Tooltip, Link, Button } from "@chakra-ui/react";
import { useState, useEffect } from "react";
import { InfoIcon } from "@chakra-ui/icons";
import { useNavigate } from "react-router-dom";
import { scrollbar } from "./extensionStore";
export default () => {
  const navigate = useNavigate();
  const [extensions, setExtensions] = useState([]);
  useEffect(() => {
    (async () => {
      const res = await window.admin.getExtensions();
      setExtensions(res);
    })();
  }, []);
  return (
    <Flex paddingTop={"48px"} margin={"auto"} direction={"column"} width={"80%"} height={"100%"}>
      <Heading marginBottom={"32px"} fontSize={32}>
        Your extensions{" "}
      </Heading>
      <Flex height={"100%"} overflowY={"auto"} direction={"column"} gap={"12px"} sx={scrollbar}>
        {extensions.map((extension) => (
          <ItemList
            name={extension.name}
            key={extension.id}
            image={extension.icon}
            navigate={navigate}
            id={extension.uuid}
          />
        ))}
        {extensions.length === 0 ? (
          
          <Flex
            direction={"column"}
            align={"center"}
            justify={"center"}
            height={"100%"}
            width={"100%"}
            gap={4}
          >
            <Text color={"flish.700"} fontWeight={600} fontSize={14}>
              üò• You don't have any extensions yet.
            </Text>
            <Button size={"sm"} >Download your {'\n'} first extension</Button>


            </Flex>
        ) : (null)}
      </Flex>
      <Text paddingTop={4} fontWeight={500} fontSize={14} margin={"auto"}>
        Above is all your extensions installed. You can install more extensions{" "}
        <Link /* onClick={navigate("/store")} */>visiting the marketplace</Link>
        . If you want to modify settings, click on an extension
      </Text>
    </Flex>
  );
};

const ItemList = ({ name, image, id, navigate }) => (
  <Flex
    tabIndex={0}
    onClick={() => navigate(`/extensionManager/${id}`)}
    width={"100%"}
    backgroundColor={"flish.400"}
    p={"16px"}
    gap={"10px"}
    borderRadius={"12px"}
    align={"center"}
    _hover={{ backgroundColor: "flish.500" }}
    _focusWithin={{ borderWidth: 2, outline: "none", borderColor: "flish.600" }}
    transition={"transition: opacity 0.5s ease-in-out;"}
    cursor={"pointer"}
/*     role={"link"} */
  >
    <Image boxSize={"24px"} borderRadius={100} src={image} />
    <Heading fontSize={15}>{name}</Heading>
  </Flex>
);
