import { Flex, Box, Text, Button } from "@chakra-ui/react";
import { useLocation, useNavigate, Link } from "react-router-dom";

export default (props: any) => {
  const settings = {
    accentuationColor: "flish.800",
    accentuationWeight: 700,
  };

  //I hate typescript sometimes, especially when it talks about JSX and React
  const url = useLocation();
  const navigate = useNavigate();
  return (
    <Flex
      align={"center"}
      justify={"center"}
      height={"100vh"}
      width={"100vw"}
      backgroundColor={"flish.200"}
    >
      <Flex
        direction={"column"}
        align={"center"}
        justify={"center"}
        width={["150px", "250px"]}
        height={"70vh"}
        p={4}
      >
        <Box>
          {Logo}
          <Text
            color={
              url.pathname === "/"
                ? settings.accentuationColor
                : "blackAlpha.600"
            }
            fontWeight={settings.accentuationWeight}
            fontSize={14}
            marginTop={4}
            _focusWithin={{
              outline: "none",
              backgroundColor: "flish.400",
              borderRadius: "4px",
            }}
            cursor={"pointer"}
            onClick={() => navigate("/")}
          >
            Home
          </Text>
          <Text
            color={
              url.pathname.startsWith("/store")
                ? settings.accentuationColor
                : "blackAlpha.600"
            }
            fontWeight={settings.accentuationWeight}
            fontSize={14}
            _focusWithin={{
              outline: "none",
              backgroundColor: "flish.400",
              borderRadius: "4px",
            }}
            cursor={"pointer"}
            onClick={() => navigate("/store")}
          >
            Extension Store
          </Text>
          <Text
            color={
              url.pathname.startsWith("/extensionManager") ||
              url.pathname.startsWith("/profile")
                ? settings.accentuationColor
                : "blackAlpha.600"
            }
            fontWeight={settings.accentuationWeight}
            fontSize={14}
            _focusWithin={{
              outline: "none",
              backgroundColor: "flish.400",
              borderRadius: "4px",
            }}
            cursor={"pointer"}
            onClick={() => navigate("/extensionManager")}
            on
          >
            Manage extensions
          </Text>
          <Text
            cursor={"pointer"}
            color={
              url.pathname.startsWith("/settings")
                ? settings.accentuationColor
                : "blackAlpha.600"
            }
            fontWeight={settings.accentuationWeight}
            fontSize={14}
            _focusWithin={{
              outline: "none",
              backgroundColor: "flish.400",
              borderRadius: "4px",
            }}
            onClick={() => navigate("/settings")}
            _focus={{ borderWidth: "1px", borderColor: "flish.500" }}
            _focusVisible={{ borderWidth: "1px", borderColor: "flish.500" }}
          >
            Settings
          </Text>
          <Text
            cursor={"pointer"}
            color={
              url.pathname.startsWith("/dev")
                ? settings.accentuationColor
                : "blackAlpha.600"
            }
            fontWeight={settings.accentuationWeight}
            fontSize={14}
            _focusWithin={{
              outline: "none",
              backgroundColor: "flish.400",
              borderRadius: "4px",
            }}
            onClick={() => navigate("/dev")}
            _focus={{ borderWidth: "1px", borderColor: "flish.500" }}
            _focusVisible={{ borderWidth: "1px", borderColor: "flish.500" }}
          >
            Developer mode
          </Text>
          <Text
            fontWeight={settings.accentuationWeight}
            fontSize={14}
            onClick={() => window.open("https://flish.dev")}
            cursor={"pointer"}
            _focusWithin={{
              outline: "none",
              backgroundColor: "flish.400",
              borderRadius: "4px",
            }}
            color={"blackAlpha.600"}
          >
            Go to website
          </Text>
        </Box>
        <Box></Box>
        <Button marginTop={8} size={"sm"} onClick={() => navigate(-1)}>
          ‚Üê
        </Button>
      </Flex>
      <Box
        padding={"24px"}
        paddingTop={"96px"}
        height={"100%"}
        minW={"900px"}
        width={"900px"}
      >
        {props.children}
      </Box>
      <Box width={"250px"}></Box>
    </Flex>
  );
};

