import { Button, Flex, Heading } from "@chakra-ui/react";
import { useNavigate } from "react-router-dom";
import { DownloadIcon, SettingsIcon, EditIcon, AddIcon, LinkIcon, ChatIcon } from "@chakra-ui/icons";

export default (props: any) => {
  const navigate = useNavigate();
  return (
    <Flex
      gap={"32px"}
      height={"100%"}
      align="center"
      justify={"flex-start"}
      direction={"column"}
    >
      <Heading width={"80%"}>
        {getGreetings()} <br /> {"\n"} What do you need ?{" "}
      </Heading>
      <Flex
        justify={"flex-start"}
        align={"flex-start"}
        overflowY={"auto"}
        gap={"32px"}
        wrap={"wrap"}
        paddingTop={16}
        width={"80%"}
        marginRight={"auto"}
        marginLeft={"auto"}
      >
        <Flex
          _hover={{ backgroundColor: "flish.400" }}
          gap={3}
          p={5}
          backgroundColor={"flish.300"}
          borderRadius={"12px"}
          direction={"column"}
          as="button"
          width={"45%"}
          onClick={() => navigate("/store")}
        >
          <DownloadIcon
            borderRadius={60}
            p={4}
            overflow={"visible"}
            color={"flish.700"}
            backgroundColor={"flish.200"}
            boxSize={"64px"}
          />
          <Heading textAlign={"left"} marginBottom={"auto"} fontSize={24}>
            I want a new extension
          </Heading>
        </Flex>
        <Flex
          _hover={{ backgroundColor: "flish.400" }}
          gap={3}
          p={5}
          backgroundColor={"flish.300"}
          borderRadius={"12px"}
          direction={"column"}
          as="button"
          width={"48%"}
          onClick={() => navigate("/extensionManager")}
        >
          <EditIcon
            borderRadius={60}
            p={4}
            overflow={"visible"}
            color={"flish.700"}
            backgroundColor={"flish.200"}
            boxSize={"64px"}
          />
          <Heading textAlign={"left"} marginBottom={"auto"} fontSize={24}>
            I want to manage my extensions
          </Heading>
        </Flex>
        <Flex
          _hover={{ backgroundColor: "flish.400" }}
          gap={3}
          p={5}
          backgroundColor={"flish.300"}
          borderRadius={"12px"}
          direction={"column"}
          as="button"
          width={"45%"}
          onClick={() => navigate("/settings")}
        >
          <SettingsIcon
            borderRadius={60}
            p={4}
            overflow={"visible"}
            color={"flish.700"}
            backgroundColor={"flish.200"}
            boxSize={"64px"}
          />
          <Heading textAlign={"left"} marginBottom={"auto"} fontSize={24}>
            I want to change settings
          </Heading>
        </Flex>
        <Flex
          _hover={{ backgroundColor: "flish.400" }}
          gap={3}
          p={5}
          backgroundColor={"flish.300"}
          borderRadius={"12px"}
          direction={"column"}
          as="button"
          width={"45%"}
          onClick={() => navigate("/dev")}
        >
          <SettingsIcon
            borderRadius={60}
            p={4}
            overflow={"visible"}
            color={"flish.700"}
            backgroundColor={"flish.200"}
            boxSize={"64px"}
          />
          <Heading textAlign={"left"} marginBottom={"auto"} fontSize={24}>
            I want to create my own extension
          </Heading>
        </Flex>
        <Flex
          _hover={{ backgroundColor: "flish.400" }}
          gap={3}
          p={5}
          backgroundColor={"flish.300"}
          borderRadius={"12px"}
          direction={"column"}
          as="button"
          width={"45%"}
          onClick={() => window.open("https://flish.dev")}
        >
          <LinkIcon
            borderRadius={60}
            p={4}
            overflow={"visible"}
            color={"flish.700"}
            backgroundColor={"flish.200"}
            boxSize={"64px"}
          />
          <Heading textAlign={"left"} marginBottom={"auto"} fontSize={24}>
            I want to go to Flish website
          </Heading>
        </Flex>
        <Flex
          _hover={{ backgroundColor: "flish.400" }}
          gap={3}
          p={5}
          backgroundColor={"flish.300"}
          borderRadius={"12px"}
          direction={"column"}
          as="button"
          width={"45%"}
          onClick={() => window.open("https://flish.dev/contact")}
        >
          <ChatIcon
            borderRadius={60}
            p={4}
            overflow={"visible"}
            color={"flish.700"}
            backgroundColor={"flish.200"}
            boxSize={"64px"}
          />
          <Heading textAlign={"left"} marginBottom={"auto"} fontSize={24}>
            I want some support or give feedback
          </Heading>
        </Flex>
        
      </Flex>
    </Flex>
  );
};

const getGreetings = () => {
  let now = new Date();
  let hour = now.getHours();
  if (hour < 12 && hour >= 5) {
    return "Good Morning";
  } else if (hour < 18) {
    return "Good Afternoon";
  } else if (hour < 22) {
    return "Good Evening";
  }
  return "Good Night";
};
