
import { Flex, Text, Image, Link, Heading, Button } from "@chakra-ui/react";
import { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { scrollbar } from "./extensionStore";

export default () => {
  const navigate = useNavigate();
  const [Data, setData] = useState(undefined);
  const [Instances, setInstances] = useState(undefined);
  const { extensionId } = useParams();
  useEffect(() => {
    (async () => {
      const data = await window.admin.getExtension(extensionId);
      const instances = await window.admin.getAllInstances();
      let ToSave = [];
      for (const key in instances) {
        if (Object.hasOwnProperty.call(instances, key)) {
          const element = instances[key];
          if (element.extensionID === extensionId) {
            //Be careful, naming convention is bad. API has extensionID, but Front end has extensionId (capitalized ID)
            ToSave.push(element);
          }
        }
      }
      setData(data);
      setInstances(ToSave);
    })();
  }, []);
  if (!Data || !Instances) {
    return <Flex>Loading...</Flex>;
  } else {
    return (
      <Flex
        direction={"column"}
        gap={8}
        p={5}
        width={"80%"}
        margin={"auto"}
        height={"100%"}
        justify={"flex-start"}
        align={"flex-start"}
      >
        <Image borderRadius={100} src={Data.icon} boxSize={24} />
        <Flex
          width={"100%"}
          marginTop={"24px"}
          gap={"16px"}
          direction={"column"}
        >
          <Heading fontSize={"36px"}>{Data.name} extension</Heading>
          <Text>{Data.description}</Text>
        </Flex>
        <Flex width={"100%"} align={"center"} justify={"space-between"}>
          <Heading color={"flish.700"} fontSize={22}>
            Profiles :
          </Heading>
          <Button
            size={"sm"}
            onClick={() => navigate("/profileNew/" + extensionId)}
          >
            +
          </Button>
        </Flex>
        <Flex
          sx={scrollbar} //Define a custom scrollbar
          overflowY={"auto"}
          gap={4}
          width={"100%"}
          direction={"column"}
        >
          {Instances.map((instance) => (
            <Flex
              onClick={() => navigate(`/profile/${instance.instanceID}`)}
              p="14px"
              width={"100%"}
              _hover={{ backgroundColor: "flish.500" }}
              borderRadius={"12px"}
              backgroundColor={"flish.400"}
              _focusWithin={{ outline: "none", backgroundColor: "flish.500" }} //Remove outline when focused
              as="button" // Could be clicked
              key={instance.instanceID}
            >
              <Text fontSize={14} fontWeight={600}>
                {instance.name}
              </Text>
            </Flex>
          ))}
          {Instances.length === 0 ? (
            <Flex direction={"column"} height={"100%"} width={"100%"} justify={"center"} align={"center"} >
              <Text color={"flish.600"} fontSize={14}>No profile found</Text>
              <Text color={"flish.600"} fontSize={14}>Would you like to create a new profile ?</Text>
              <Button size={"sm"} marginTop={4}  onClick={() => navigate("/profileNew/" + extensionId)} >New profile</Button>
            </Flex>
          ) : (null) }
        </Flex>
      </Flex>
    );
  }
};
