
import { useState } from "react";
import { useParams } from "react-router-dom";
import {
  Flex,
  Heading,
  Text,
  CircularProgress,
  Button,
  Link,
  Image,
} from "@chakra-ui/react";
import { CheckIcon } from "@chakra-ui/icons";
import useSWR from "swr";
import { useNavigate } from "react-router-dom";
import { scrollbar } from "./extensionStore";

const { extensionApiURL } = window.admin.getConfig();
export default () => {
  const navigate = useNavigate();
  const { extensionId } = useParams();
  const [alreadyInstalled, setAlreadyInstalled] = useState(false);
  window.admin.getExtension(extensionId).then((extension) => {
    if (extension) {
      setAlreadyInstalled(true);
    }
  });
  const fetcher = (...args) => fetch(...args).then((res) => res.json());
  const { data, err } = useSWR(`${extensionApiURL}/${extensionId}`, fetcher);
  if (err)
    return (
      <Flex height={"100%"} justify={"center"} align={"center"}>
        <Text textAlign={"center"} width={"30%"}>
          We can't fetch Flish Api. Check your internet connection or
          <Text fontWeight={600}>
            {" "}
            <Link href="https://status.flish.dev" target="_blank">
              Flish Status
            </Link>
          </Text>
        </Text>
      </Flex>
    );
  if (!data)
    return (
      <Flex
        direction={"column"}
        height={"100%"}
        justify={"center"}
        align={"center"}
      >
        <CircularProgress
          marginBottom={8}
          color="flish.700"
          isIndeterminate
          size={10}
        />
        <Text fontWeight={600}>Fetching data...</Text>
      </Flex>
    );
  return (
    <Flex
      height={"100%"}
      justify={"space-between"}
      direction={"column"}
      align={"center"}
      overflowY={"auto"}
      p={4}
      sx={scrollbar}
    >
      <Flex gap={8} align={"center"}>
        <Image
          alt="The icon of the extension"
          src={data.icon}
          size={50}
          borderRadius={100}
        />
        <Heading marginTop={4} fontSize={28}>
          About <Text color={"flish.800"}>{data.name}</Text>
        </Heading>
        {alreadyInstalled ? ( //Show a check icon if the extension is already installed, if not return null
          <Flex marginTop={"auto"} align={"center"} gap={2}>
            <CheckIcon />{" "}
            <Text color={"flish.700"} fontWeight={700} fontSize={14}>
              Installed
            </Text>
          </Flex>
        ) : null}
      </Flex>
      <Flex
        align="center"
        width={"80%"}
        direction={"column"}
        gap={4}
        marginTop={8}
        height={"100%"}
      >
        <Image
          borderRadius={"10px"}
          width={"546px"}
          height={"312px"}
          src={data.header}
          alt="An image showing capabilities of the extension"
        />
        <Text>{data.description}</Text>
      </Flex>

      <Flex
        marginBottom={4}
        p={3}
        borderRadius={"12px"}
        backgroundColor={"flish.300"}
        width={"80%"}
        direction={"column"}
        gap={4}
        marginTop={8}
      >
        <Link href={`https://flish.dev/store/author/${data.author}`}>
          {" "}
          <Text fontWeight={600}>Made by {data.author}</Text>{" "}
        </Link>
        <Text fontWeight={600}>
          Extension commands : {data.textSuggestion.map((a) => a + ", ")}
        </Text>
      </Flex>
      <Button
        onClick={
          alreadyInstalled //If already installed, go to extension manager, if not, install it
            ? () => navigate(`/extensionManager/${extensionId}`)
            : () => navigate(`/storeInstall/${extensionId}`)
        }
        width={"80%"}
      >
        {alreadyInstalled ? "Change settings" : "Install"}
      </Button>
    </Flex>
  );
};
