import {
  Flex,
  Heading,
  Alert,
  AlertDescription,
  AlertIcon,
  Text,
  FormControl,
  Input,
  FormLabel,
  FormHelperText,
  FormErrorMessage,
  Button,
  Link,
  Textarea,
  useToast,
  Checkbox,
} from "@chakra-ui/react";
import { ExternalLinkIcon } from "@chakra-ui/icons";
import { useState, useEffect } from "react";

export default () => {
  const [URLValue, setURLValue] = useState("http://localhost:3000");
  const [URLInvalid, setURLInvalid] = useState(false);
  const [mockAPI, setMockAPI] = useState("{}");
  const [mockAPIInvalid, setMockAPIInvalid] = useState(false);
  const [Query, setQuery] = useState("");
  const [Headless, setHeadless] = useState(false);

  const toast = useToast();
  useEffect(() => {
    (async () => {
      const url = await window.admin.getURLDevMode();
      if (url) {
        setURLValue(url);
      }
      const mockDevMode = await window.admin.getMockDevMode();
      if (mockDevMode) {
        setMockAPI(JSON.stringify(mockDevMode, null, 2));
      }
      const query = await window.admin.getQueryDevMode();
      if (query) {
        setQuery(query);
      }
      const headless = await window.admin.getHeadlessDevMode();
      if (headless) {
        setHeadless(headless);
      }
    })();
  }, []);
  const saveData = async () => {
    if (!URLInvalid && !mockAPIInvalid) {
      await window.admin.setURLDevMode(URLValue).catch((a) => {
        toast({
          status: "error",
          title: "Error",
          description: a.message,
          duration: 5000,
        });
      });
      await window.admin.setMockDevMode(mockAPI).catch((a) => {
        toast({
          status: "error",
          title: "Error",
          description: a.message,
          duration: 5000,
        });
      });
      await window.admin.setQueryDevMode(Query).catch((a) => {
        toast({
          status: "error",
          title: "Error",
          description: a.message,
          duration: 5000,
        });
      });
      await window.admin.setHeadlessDevMode(Headless).catch((a) => {
        toast({
          status: "error",
          title: "Error",
          description: a.message,
          duration: 5000,
        });
      });
      toast({
        status: "success",
        title: "Success",
        description: "Saved",
        duration: 5000,
      });
    } else {
      toast({
        title: "Error",
        description: "Invalid URL or Mock API",
        status: "error",
        duration: 5000,
      });
    }
  };

  const handleURLChange = (e) => {
    const url = e.target.value;
    setURLValue(url);
    if (url.startsWith("http://") || url.startsWith("https://")) {
      setURLInvalid(false);
    } else {
      setURLInvalid(true);
    }
  };
  const handleMockAPIChange = (e) => {
    const mockAPI = e.target.value;
    setMockAPI(mockAPI);
    try {
      JSON.parse(mockAPI);
      setMockAPIInvalid(false);
    } catch (error) {
      setMockAPIInvalid(true);
    }
  };
  return (
    <Flex
      paddingTop={"48px"}
      margin={"auto"}
      direction={"column"}
      width={"85%"}
      height={"100%"}
      overflowY={"auto"}
      p={2}
    >
      <Heading marginBottom={"32px"} fontSize={32}>
        Debug an extension
      </Heading>
      <Alert backgroundColor={"flish.300"} status="info">
        <AlertIcon color={"flish.600"} />
        <AlertDescription
          fontSize={14}
          fontWeight={500}
          color={"blackAlpha.700"}
        >
          Restart is needed to apply changes.
        </AlertDescription>
      </Alert>
      <FormControl isInvalid={URLInvalid} marginTop={8}>
        <FormLabel htmlFor="url">URL</FormLabel>
        <Input
          value={URLValue}
          onChange={handleURLChange}
          id="url"
          placeholder="https://localhost:8080"
        />
        <FormHelperText>
          Extension are loaded from a remote URL. Default to localhost:3000
        </FormHelperText>
        <FormErrorMessage>
          This can't be empty or not starting by http:// or https://
        </FormErrorMessage>
      </FormControl>
      <FormControl marginTop={4}>
        <FormLabel htmlFor="Query">Query</FormLabel>
        <Input
          value={Query}
          onChange={(e) => setQuery(e.target.value)}
          id="Query"
          placeholder="Where is Mjolnir ?"
        />
        <FormHelperText>
          Before opening an extension, you can write a sentence to search for.
          This replaces the default search. Default to " "
        </FormHelperText>
      </FormControl>
      <FormControl marginTop={4}>
        <FormLabel htmlFor="Query">Headless</FormLabel>
        <Checkbox isChecked={Headless} onChange={() => setHeadless(!Headless)}>
          Should the extension be in headless mode ?
        </Checkbox>
        <FormHelperText>
          Some extensions could be in headless mode. They can be considered as a
          browser without interface. You can still access html and css but they
          won't be displayed. Default to false
        </FormHelperText>
      </FormControl>
      <FormControl isInvalid={mockAPIInvalid} marginTop={4}>
        <FormLabel htmlFor="url">Mock configuration</FormLabel>
        <Textarea
          backgroundColor={"flish.400"}
          placeholder='{"apiKey":"abcdefghij...", "apiSecret":"abcdefghij..."}'
          value={mockAPI}
          onChange={handleMockAPIChange}
        />
        <FormHelperText>
          To emulate environment variable of the extension, you could use a JSON
          object representing your configuration. The key represents the name of
          the variable and the value represents the value. Default to {"{}"}
        </FormHelperText>
        <FormErrorMessage>This should be a valid JSON object.</FormErrorMessage>
      </FormControl>
      <Alert marginTop={"auto"} backgroundColor={"flish.300"} status="info">
        <AlertIcon color={"flish.600"} />
        <AlertDescription
          fontSize={14}
          fontWeight={500}
          color={"blackAlpha.700"}
        >
          An extension in debug mode has access to all permissions without
          restriction. Be careful !
          <br />
          You can only have one extension in debug mode at a time.
          <br />
          We recommend looking at the{" "}
          <Link color={"flish.700"} href="https://flish.dev/docs">
            documentation <ExternalLinkIcon />
          </Link>{" "}
          to learn
        </AlertDescription>
      </Alert>
      <Button
        isDisabled={mockAPIInvalid || URLInvalid}
        marginTop={4}
        width={"100%"}
        onClick={saveData}
      >
        {URLInvalid || mockAPIInvalid ? "Invalid" : "Save"}
      </Button>
    </Flex>
  );
};
