import * as React from "react";
import ReactDOM from "react-dom";
import { App } from "./App";
import { HashRouter, Routes, Route } from "react-router-dom";
import reportWebVitals from "./reportWebVitals";
import * as serviceWorker from "./serviceWorker";
import { ChakraProvider, Box } from "@chakra-ui/react";
import flishTheme from "@flish/chakra-theme";
import Layout from "./components/layout";
import {
  Accueil,
  ExtensionManager,
  ExtensionManagerId,
  ExtensionStore,
  ExtensionStoreId,
  InstallationStatus,
  ProfileNew,
  ProfileUpdate,
  SettingsPage,
  Dev
} from "./pages/index";

ReactDOM.render(
  <React.StrictMode>
    <ChakraProvider theme={flishTheme}>
      <HashRouter>
        <Layout>
          <Routes>
            <Route path="/" element={<Accueil />} /* Extension store Page*/ />
            <Route path="extensionManager" element={<ExtensionManager />} />
            <Route
              path="extensionManager/:extensionId"
              element={<ExtensionManagerId />}
            />
            <Route path="store" element={<ExtensionStore />} />
            <Route path="store/:extensionId" element={<ExtensionStoreId />} />
            <Route
              path="storeInstall/:extensionId"
              element={<InstallationStatus />}
            />
            <Route path="profileNew/:extensionId" element={<ProfileNew />} />
            <Route path="profile/:id" element={<ProfileUpdate />} />
            <Route path="settings" element={<SettingsPage />} />
            <Route path="dev" element={<Dev />} />
          </Routes>
        </Layout>
      </HashRouter>
    </ChakraProvider>
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorker.unregister();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
