import {
  Flex,
  Heading,
  Text,
  FormControl,
  Input,
  FormLabel,
  FormHelperText,
  UnorderedList,
  ListItem,
  Link,
  Code,
  Button,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  useToast,
} from "@chakra-ui/react";
import { ExternalLinkIcon } from "@chakra-ui/icons/";
import { useState, useEffect } from "react";
export default () => {
  const [Shortcut, setShortcut] = useState("ALT+P");
  const toast = useToast();
  //u => User analytics | s => Security analytics
  const [U, setU] = useState(false);
  const [S, setS] = useState(false);
  useEffect(() => {
    (async () => {
      const { shortcut } = await window.admin.getSettings();
      setShortcut(shortcut);
    })();
  }, []);
  return (
    <Flex
      gap={8}
      direction="column"
      height="100%"
      width={"80%"}
      margin={"auto"}
      p={5}
    >
      <Heading>Settings</Heading>
      <FormControl marginTop={"32px"}>
        <FormLabel htmlFor="name">Shortcut</FormLabel>
        <Input
          id="name"
          value={Shortcut}
          onChange={(e) => setShortcut(e.target.value)}
        />
        <FormHelperText>
          Shortcut to open the search bar. A restart is required to apply.
        </FormHelperText>
        <FormHelperText>
          Syntax : <Code>{`[modfier] + [key]`}</Code> <br />
          Example : <Code>{`ALT + P`}</Code>
          <FormHelperText>
            To know available shortcut, check{" "}
            <Link
              color={"flish.700"}
              href="https://electronjs.org/docs/api/accelerator"
              isExternal
            >
              Electron documentation <ExternalLinkIcon />
            </Link>
          </FormHelperText>
        </FormHelperText>
      </FormControl>
      <Flex align={"center"} justify={"space-between"}>
        <Text fontSize={14} fontWeight={700}>
          User analytics
        </Text>
        <Button onClick={() => setU(true)} size={"sm"}>
          Configure
        </Button>
      </Flex>
      <Flex align={"center"} justify={"space-between"}>
        <Text fontSize={14} fontWeight={700}>
          Security analytics
        </Text>
        <Button onClick={() => setS(true)} size={"sm"}>
          Configure
        </Button>
      </Flex>
      <Button
        marginTop={"auto"}
        onClick={() => {
          window.admin.setShortcut(Shortcut).then(() => {
            toast({
              title: "Shortcut updated to " + Shortcut,
              description: "Restart the app to apply",
              status: "success",
              duration: 9000,
              isClosable: true,
            });
          });
        }}
        variant={"white"}
      >
        Save
      </Button>
      <Modal
        closeOnOverlayClick={false}
        closeOnEsc={false}
        isCentered
        isOpen={U}
        onClose={() => setU(false)}
      >
        <ModalOverlay />
        <ModalContent backgroundColor={"flish.300"}>
          <ModalHeader>Would you like to enable user analytics ?</ModalHeader>
          <ModalBody>
            <Heading fontSize={16}>
              We value your privacy : data is anonymous
            </Heading>
            <Text marginTop={2} fontSize={14} color={"blackAlpha.700"}>
              We know that most privacy policy looks boring.{" "}
              <Text as="span" color={"flish.700"}>
                We just kindly ask you to enable user analytics. This helps us
                building a better product.{" "}
              </Text>{" "}
              We don't use Google analytics (or GAFAM like things) so that data
              stay on our server. If you want to know what we do with data, :{" "}
              <Link href="https://flish.dev/blog" isExternal>
                check our blog <ExternalLinkIcon />
              </Link>
            </Text>
            <Heading marginBottom={2} marginTop={6} fontSize={16}>
              We will only collect the following data :
            </Heading>

            <UnorderedList fontSize={14} color={"blackAlpha.700"}>
              <ListItem>Screen size</ListItem>
              <ListItem>Platform (windows, macOS, Linux)</ListItem>
              <ListItem>Country</ListItem>
              <ListItem>App version</ListItem>
              <ListItem>
                Some events (App started, extension installed, extension
                launched, etc.)
              </ListItem>
            </UnorderedList>
          </ModalBody>
          <ModalFooter>
            <Button
              fontWeight={300}
              backgroundColor={"transparent"}
              mr={3}
              onClick={() => {
                window.admin.setTelemetry(false);
                setU(!U);
              }}
            >
              I refuse
            </Button>
            <Button
              onClick={() => {
                window.admin.setTelemetry(true);
                setU(!U);
              }}
              backgroundColor={"flish.500"}
              autoFocus
              variant="ghost"
            >
              I accept
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
      <Modal
        closeOnOverlayClick={false}
        closeOnEsc={false}
        isCentered
        isOpen={S}
        onClose={() => setS(false)}
      >
        <ModalOverlay />
        <ModalContent backgroundColor={"flish.300"}>
          <ModalHeader>
            Would you like to enable security analytics ?
          </ModalHeader>
          <ModalBody>
            <Heading fontSize={16}>
              We value your privacy and also your security
            </Heading>
            <Text marginTop={2} fontSize={14} color={"blackAlpha.700"}>
              Each extension has a set of permissions. We extensively inspect
              each extension before any publish. However, to prevent any
              malicious use, we log each API call from an extension. We then
              analyse this data to see if there is any suspicious activity.
              Accepting this will allow us to enhance security and help us to
              improve our product.
            </Text>
          </ModalBody>
          <ModalFooter>
            <Button
              fontWeight={300}
              backgroundColor={"transparent"}
              mr={3}
              onClick={() => {
                window.admin.setSecurityLog(false);
                setS(!S);
              }}
            >
              I refuse
            </Button>
            <Button
              onClick={() => {
                window.admin.setSecurityLog(true);
                setS(!S);
              }}
              backgroundColor={"flish.500"}
              autoFocus
              variant="ghost"
            >
              I accept
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </Flex>
  );
};
