
import {
  Flex,
  Heading,
  Text,
  CircularProgress,
  Image,
  Link,
} from "@chakra-ui/react";
import { CheckIcon } from "@chakra-ui/icons";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { useEffect, useState, Fragment } from "react";

const { extensionApiURL } = window.admin.getConfig();
export default () => {
  const [Data, setData]: [Data: any, setData: Function] = useState([]);
  const [Extension, setExtension]: [any[], Function] = useState([]);
  const navigate = useNavigate();
  useEffect(() => {
    (async () => {
      setExtension(await window.admin.getExtensions());
      const { data } = await axios.get(extensionApiURL);
      setData(data);
    })();
  }, []);
  if (Data.length === 0) {
    return (
      <Flex
        gap={4}
        height={"100%"}
        justify={"center"}
        align={"center"}
        direction={"column"}
        
      >
        <Heading fontSize={22}>Fetching extensions from marketplace</Heading>
        <CircularProgress color="flish.700" isIndeterminate />
        <Text textAlign={"center"} width={"30%"}>
          If this takes too long, check your internet connection or
          <Link href="http://status.flish.dev" target="_blank" isExternal>
            {" "}
            Flish Status
          </Link>
        </Text>
      </Flex>
    );
  } else {
    console.log(Extension);
    return (
      <Flex width={"100%"} height={"100%"} direction={"column"}>
        <Heading marginBottom={8} fontSize={20}>
          Find a new extension
        </Heading>
        <Flex
          overflowY={"auto"}
          width={"100%"}
          gap={4}
          wrap={"wrap"}
          sx={scrollbar}
        >
          {Data.map((d: any) => (
            <Flex
              direction={"column"}
              width={"32%"}
              padding={"24px"}
              backgroundColor={"flish.100"}
              borderRadius={"12px"}
              cursor={"pointer"}
              onClick={() => navigate(`/store/${d.uuid}`)}
              key={d.uuid}
              /* tabIndex={0}
              role={"button"} */
            >
              <Image
                src={d.icon}
                width={"48px"}
                height={"48px"}
                borderRadius={100}
              />
              <Heading marginTop={"24px"} fontSize={18}>
                {d.name}
              </Heading>
              <Text marginBottom={4} marginTop={"12px"} fontSize={14}>
                {truncate(d.description, 80)}
              </Text>
              {Extension.find((a) => a.uuid === d.uuid) === undefined ? ( //If extension is installed
                <Text></Text>
              ) : (
                <Flex marginTop={"auto"} align={"center"} gap={2} >
                  <CheckIcon />{" "}
                  <Text color={"flish.700"} fontWeight={700} fontSize={14}>
                    Installed
                  </Text>
                </Flex>
              )}
            </Flex>
          ))}
        </Flex>
      </Flex>
    );
  }
};

declare global {
  interface Window {
    admin: any;
  }
}
//https://stackoverflow.com/questions/1199352/smart-way-to-truncate-long-strings
function truncate(str: string, n: number) {
  return str.length > n ? str.substring(0, n) + "..." : str;
}

const scrollbar = {
  "::-webkit-scrollbar": {
    backgroundColor: "transparent",
    width: "6px",
  },
  "::-webkit-scrollbar-thumb": {
    backgroundColor: "#B9B4FF",
    borderRadius: "6px",
  },
};
export { scrollbar };
