
import {
  Flex,
  Text,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  Button,
  CircularProgress,
  Link,
  Heading,
  ListItem,
  Alert,
  AlertIcon,
  useToast,
} from "@chakra-ui/react";
import { Fragment, useEffect, useState } from "react";
import { useNavigationType, useNavigate, useParams } from "react-router-dom";
import useSWR from "swr";

const { extensionApiURL } = window.admin.getConfig();
export default () => {
  const toast = useToast();
  const [currentlyInstalling, setCurrentlyInstalling] = useState(null);
  const { isOpen, onOpen } = useDisclosure();
  const navigate = useNavigate();
  const { extensionId } = useParams();
  const fetcher = (...args) => fetch(...args).then((res) => res.json());
  const { data, err } = useSWR(`${extensionApiURL}/${extensionId}`, fetcher);
  function installExtension() {
    setCurrentlyInstalling(true);
    window.admin
      .installExtension(extensionId)
      .then(async () => {
        await new Promise((resolve) => setTimeout(resolve, 1000)); //Solve a strange bug. Don't ask why. SOmetimes, config store doesn't get updated at the right time. So we wait a bit.
        setCurrentlyInstalling(false);
        toast({
          title: "Success",
          description: "Extension installed. Now configure a profile",
          status: "success",
          duration: 5000,
        });
        navigate(`/profileNew/${extensionId}`);
      })
      .catch((e) => {
        setCurrentlyInstalling(false);
        toast({
          title: "Error",
          description: e.message,
          status: "error",
          duration: 5000,
        });
      });
  }
  function onClose() {
    navigate(-1);
  }
  if (useNavigationType() == "POP") {
    useEffect(() => {
      navigate(-1);
    }, []);
    return (
      <Flex align={"center"} justify={"center"}>
        <Text>This page has already been called</Text>
      </Flex>
    );
  } else if (err) {
    return (
      <Flex height={"100%"} justify={"center"} align={"center"}>
        <Text textAlign={"center"} width={"30%"}>
          We can't fetch Flish Api. Check your internet connection or
          <Text fontWeight={600}>
            {" "}
            <Link href="https://status.flish.dev" target="_blank">
              Flish Status
            </Link>
          </Text>
        </Text>
      </Flex>
    );
  } else if (!data) {
    return (
      <Flex
        direction={"column"}
        height={"100%"}
        justify={"center"}
        align={"center"}
      >
        <Text textAlign={"center"} width={"30%"}>
          Loading...
        </Text>
        <CircularProgress isIndeterminate color="flish.700" />
      </Flex>
    );
  } else {
    useEffect(() => {
      onOpen();
    }, []);
    return (
      <Fragment>
        <Flex align={"center"} direction={"column"}>
          <Heading>Installation status</Heading>
          <CircularProgress visibility={currentlyInstalling} isIndeterminate />
          <Text></Text>
        </Flex>
        <Modal
          isCentered
          closeOnOverlayClick={false}
          closeOnEsc={false}
          isOpen={isOpen}
          onClose={onClose}
        >
          <ModalOverlay />
          <ModalContent backgroundColor={"flish.200"}>
            <ModalHeader fontWeight={700}>Permission Request</ModalHeader>
            <ModalCloseButton />
            <ModalBody display={"flex"} flexDirection={"column"} gap={2}>
              <Text fontSize={14} color={"flish.700"} fontWeight={600}>
                Each extension has a set of permission. Permissions listed below
                are necessary for extension execution. However, if you don't
                want to authorize the extension, you could go back and refuse.
              </Text>
              <Text fontSize={16} fontWeight={700}>
                Permissions asked :{" "}
              </Text>
              {data.permissions.map((a) => (
                <ListItem fontSize={14} key={a}>
                  {explaination(a)}
                </ListItem>
              ))}
              <Alert
                backgroundColor={"flish.300"}
                borderRadius={"12px"}
                status="warning"
              >
                <AlertIcon />
                Be careful with extension permissions. Some could put your
                computer at risk
              </Alert>
            </ModalBody>

            <ModalFooter>
              <Button variant={"white"} mr={3} onClick={onClose}>
                I refuse
              </Button>
              <Button onClick={installExtension}>I accept</Button>
            </ModalFooter>
          </ModalContent>
        </Modal>
      </Fragment>
    );
  }
};

function explaination(a) {
  if (a === "fs") {
    return "In order to read and write files, extension needs the fs permission";
  } else if (a === "media") {
    return "The media permission is required.";
  } else if (a === "clipboard") {
    return "In order to read and write to the clipboard, extension needs the clipboard permission.";
  } else if (a === "notifications") {
    return "In order to send notifications, extension needs the notifications permission.";
  } else if (a === "geolocation") {
    return "In order to access your location, extension needs the geolocation permission.";
  } else {
    return ` Unrecognized permission : ${a}`;
  }
}
