import { useEffect, useState, Fragment } from "react";
import {
  Flex,
  Heading,
  Alert,
  Image,
  AlertDescription,
  AlertIcon,
  Text,
  Editable,
  FormControl,
  CircularProgress,
  Input,
  FormLabel,
  FormHelperText,
  FormErrorMessage,
  Button,
  Divider,
  useToast,
} from "@chakra-ui/react";
import { useParams, useNavigate } from "react-router-dom";

export default () => {
  const { id } = useParams();
  const [Extension, setExtension] = useState<extension>();
  const [FormValue, setFormValue]: [any, Function] = useState({
    name: "An awesome name",
    envVariable: {},
  });
  const navigate = useNavigate();
  const toast = useToast();
  useEffect(() => {
    (async () => {
      const { extension, instance } = await window.admin.getInstance(id);
      setExtension(extension);
      setFormValue({
        ...FormValue,
        name: instance.name,
      });
      const envVariable = await window.admin.getEnvVariable(id);
      let data = {...FormValue};
      for (const key in envVariable) {
          if (Object.prototype.hasOwnProperty.call(envVariable, key)) {
              const element = envVariable[key];
              data.envVariable[key] = element;
              console.log(key, element);
              
          }
      }
      setFormValue(data);
    })();
  }, []);

  if (Extension === undefined || Extension === null) {
    return (
      <Flex
        gap={4}
        direction={"column"}
        justify={"center"}
        height={"100%"}
        align={"center"}
      >
        <CircularProgress color="flish.700" isIndeterminate size={24} />
        <Text fontSize={18} fontWeight={600}>
          üëã We are loading data...
        </Text>
        <Text fontSize={14}>
          If this takes too long, profile might be deleted.
        </Text>
        <Button size={"sm"} onClick={() => navigate(-1)}>
          Go back
        </Button>
      </Flex>
    );
  } else {
      console.log(FormValue);
      
    return (
      <Flex width={"80%"} height={"100%"} overflowY={"auto"} margin={"auto"} direction={"column"} p={5} gap={"32px"}>
        <Heading fontWeight={600} color={"blackAlpha.600"} fontSize={24}>
          Update profile for extension{" "}
          <Heading as="p" fontSize={32} color={"flish.700"}>
            {Extension.name}
          </Heading>
        </Heading>
        <FormControl
          isInvalid={FormValue.name === undefined || FormValue.name === ""}
          isRequired
        >
          <FormLabel htmlFor="name">Name</FormLabel>
          <Input
            id="name"
            value={FormValue.name}
            onChange={(e) => {
              setFormValue({
                ...FormValue,
                name: e.target.value,
              });
            }}
          />
          <FormHelperText>
            The name used in the search bar. We recommend to choose a meaningful
            name because you will type it every time you need to use this
            profile
          </FormHelperText>
          <FormErrorMessage>Sorry but name can't be empty</FormErrorMessage>
        </FormControl>
        <Flex
          overflowY={"auto"}
          marginBottom={6}
          gap={8}
          height={"100%"}
          width={"100%"}
          direction={"column"}
          backgroundColor={"flish.300"}
          p={4}
          borderRadius={"12px"}
        >
          {Extension.envVariables.map((envVariable) => {
            return (
              <FormControl
                isInvalid={
                  FormValue.envVariable[envVariable.name] === undefined ||
                  FormValue.envVariable[envVariable.name].length === 0
                }
                isRequired
              >
                <FormLabel htmlFor={envVariable.name}>
                  {envVariable.name}
                </FormLabel>
                <Input
                  placeholder={envVariable.placeholder}
                  id={envVariable.name}
                  value={FormValue.envVariable[envVariable.name]}
                  type={envVariable.needToBeEncrypted ? "password" : "text"}
                  onChange={(e) => {
                    const value = {
                      ...FormValue,
                      envVariable: {
                        ...FormValue.envVariable,
                        [envVariable.name]: e.target.value,
                      },
                    };
                    setFormValue(value);
                  }}
                />
                <FormHelperText>{envVariable.description}</FormHelperText>
              </FormControl>
            );
          })}
        </Flex>
        <Button
          onClick={() =>
            handleSubmit(
              toast,
              FormValue.name,
              id,
              FormValue.envVariable,
              Extension.envVariables,
              navigate
            )
          }
          width={"100%"}
        >
          Update profile
        </Button>
      </Flex>
    );
  }
};


async function handleSubmit(
    toast: Function,
    name: string,
    instanceId: string|undefined,
    storedValue: any,
    extensionVariable: envVariables[],
    navigate: Function
  ) {
    
    let dataToSaved: parametersProfile[] = [];
    let toPush = false; //In case an error is detected in form validation, function stops sending data to the backend
    for (let i = 0; i < extensionVariable.length; i++) {
      const element = extensionVariable[i];
      if (storedValue[element.name] === undefined) {
        toast({
          title: "Error",
          description: `${element.name} is not defined`,
          status: "error",
        });
        toPush = false;
        return;
      }
      dataToSaved.push({
        name: element.name,
        value: storedValue[element.name],
        needToBeEncrypted: element.needToBeEncrypted,
      });
      toPush = true;
    }

    if (toPush) {
  
      window.admin
        .updateInstance(instanceId, {name:name, envVariable:dataToSaved})
        .then(() => {
          toast({
            title: "Success",
            description: "Profile updated",
            status: "success",
          });
          navigate("/extensionManager");
        })
        .catch((error: any) => {
          toast({
            title: "Error",
            description: error.message,
            status: "error",
          });
          console.error(error);
        });
    }
  }

interface parametersProfile {
  name: string;
  value: string;
  needToBeEncrypted: boolean;
}

interface envVariables {
  /** In the form, name will be the label. Also, it will be the identifier when an extension call the env variable API */
  name: string;
  /** In the form, this will influence appearance (using a checkbox e.g.). Default to string */
  type?: "string" /* | "number" | "boolean"; */;
  /** If required, user can't skip the input. Label will have a small red star */
  /* required?: boolean; */
  /** What would be shown below the input */
  description?: string;
  /** A default value to fallback if no input. If not specified in boolean, fallback to false */
  defaultValue?: any;
  /** Value to save */
  value?: string;
  /** In case of "number" or "string", the placeholder is what would be shown in the input until nothing is written */
  placeholder?: string;
  /** If set to true, value will be saved in keychain (or platform equivalent for linux or windows) */
  needToBeEncrypted: boolean;
}

interface extension {
  /** The Unique ID of the extension generated by the server */
  uuid: string;
  /** The name of the extension */
  name: string;
  /** The description of the extension */
  description: string;
  /** The download URL of the extension */
  downloadURL: string;
  /** The version of the extension */
  version: string;
  /** Hash over extension files  */
  hash: string;
  /** The author of the extension */
  author?: string;
  /** The url of the extension */
  link: string;
  /** Absolute path to the extension */
  path: string;
  /** The mode of the app | search => app is shown but search bar is still shown, normal => on enter pressed, focus is given to extension and search bar disappear, noArgument => when choosen, no argument could be given */
  mode?: "search" | "normal" | "noArgument" | "headless";
  /** The HTML file name to load in the window */
  entry?: string;
  /** The icon of the extension */
  icon: string;
  /** An app could have multiple functionnalities. These are suggestion offered by the interface (e.g /notion search => If you want search to be recommend, put it in the array)  */
  textSuggestion?: string[];
  /** Permissions required by the extension. Specify the capabilities of the extension (could be empty)  */
  permissions: (
    | "notifications"
    | "clipboard"
    | "geolocation"
    | "media"
    | "fs"
  )[];
  /** Array of environment Variable required by the extension. User will be prompt answering them (could be empty) */
  envVariables: envVariables[];
  /** If the extension is premium */
  premium?: boolean;
}
